// Code for Material Input 

.group {
    position: relative;
    margin-bottom: 20px;

    input {
        font-size: 18px;
        padding: 10px 10px 10px 5px;
        display: block;
        width: 300px;
        border: none;
        border-bottom: 1px solid #757575;

        &:focus {
            outline: none;

            ~label {
                top: -20px;
                font-size: 14px;
                color: #5264AE;
            }
        }

        &:valid {
            ~label {
                top: -20px;
                font-size: 14px;
                color: #5264AE;
            }
        }
    }

    label {
        color: #999;
        font-size: 18px;
        font-weight: normal;
        position: absolute;
        pointer-events: none;
        left: 5px;
        top: 10px;
        transition: 0.2s ease all;
        -moz-transition: 0.2s ease all;
        -webkit-transition: 0.2s ease all;
    }

    .bar {
        position: relative;
        display: block;
        width: 300px;
    }

    .bar:before,
    .bar:after {
        content: ' ';
        height: 2px;
        width: 0px;
        position: absolute;
        background: #5264AE;
        transition: 0.2s ease all;
        -webkit-transition: 0.2s ease all;
        -moz-transition: 0.2s ease all;
    }

    .bar:before {
        left: 50%;
    }

    .bar:after {
        right: 50%;
    }

    input:focus~.bar:before,
    input:focus~.bar:after {
        width: 50%;
    }

    .highlight {
        position: absolute;
        height: 60%;
        width: 100px;
        top: 25%;
        left: 0;
        pointer-events: none;
        opacity: 0.5;
    }

    input:focus~.highlight {
        -webkit-animation: inputHighlighter 0.3s ease;
        -moz-animation: inputHighlighter 0.3s ease;
        animation: inputHighlighter 0.3s ease;
    }
    // Animation
    @-webkit-keyframes inputHighlighter{
        from{background : #5264AE;}
        to {width: 0;background: transparent;}
    }
    @-moz-keyframes inputHighlighter{
        from{background : #5264AE;}
        to {width: 0;background: transparent;}
    }
    @keyframes inputHighlighter{
        from{background : #5264AE;}
        to {width: 0;background: transparent;}
    }
}